.Backdrop_backdrop__1GJSz {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    background: rgb(0, 0, 0);
    opacity: 0.6;
}

.Backdrop_show__1_y1L {
    -webkit-animation: Backdrop_appear___E8VB 400ms forwards;
            animation: Backdrop_appear___E8VB 400ms forwards;
}

.Backdrop_hide__21vuo {
    -webkit-animation: Backdrop_disappear__3FjS8 400ms forwards;
            animation: Backdrop_disappear__3FjS8 400ms forwards;
}

@-webkit-keyframes Backdrop_appear___E8VB {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 0.6;
    }
}

@keyframes Backdrop_appear___E8VB {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 0.6;
    }
}

@-webkit-keyframes Backdrop_disappear__3FjS8 {
    0% {
        opacity: 0.6;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 0;
    }
}

